<template name="NextEvent">
  <v-card>
    <v-card-title><h2>The Next Meet</h2></v-card-title>
    <v-card-text>
      <p>{{ getNextMeetDate() }}</p>
      <slot/>
    </v-card-text>
  </v-card>

</template>

<script>
/* eslint-disable */
/* eslint import/no-webpack-loader-syntax: off */
// eslint-disable-next-line

export default {
  data() {
    return {};
  },
  methods: {
    getNextMeetDate() {
      var d = new Date();
      var now = new Date();

      now.setHours(0, 0, 0, 0);

      d.setDate(d.getDate() + (13 - d.getDay()) % 7);

      if (d < now) {
        d.setMonth(d.getMonth() + 1);
        d.setDate(d.getDate() + (13 - d.getDay()) % 7);
      }

      d.setHours(13);

      return d.toDateString();
    },
  }
};
</script>

